import React, { Component } from 'react';
import financeImg from '../assets/finance.png';

class Finance extends Component {
  render() {
    return (
      <div className='financeContainer pb-5'>
        <div className='container-fluid'>
          <div className='row text-center pt-5'>
            <div className='col-sm-6 pt-5 financeTextContainer'>
              <h1 className='financeHeading display-2'>jak je cena mch slueb?</h1>
              <p className='financeSubHeading pt-3'>
              Kad klient je jedinen, a proto pistupuji k cenn mch slueb individuln. Pevn cena se tak ned pedem urit.
              </p>
              <p className='financeText pt-3 pb-4'>
              Pestoe se cena m prce odvj od individulnch poteb kadho klienta a nemohu nabdnout pevnou tarifu, existuje urit cenov rozpt, kter me slouit jako orientan bod. Jsem vdy pipraven sednout si a probrat, co pesn potebujete, abychom spolen nali nejlep een odpovdajc vaim poadavkm i rozpotu. Cena se pot odvj od rozsahu a komplexnosti poadovanch slueb.
              </p>
              <a href='#' className='pt-5'>Kontaktujte mn</a>
            </div>
            <div className='col-sm-6'>
              <img src={financeImg} />
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Finance;